.App {
  width : 680px;
}
@media screen and (min-width : 900px){
  .App{
    margin-left : 20%;
  }
}
@media screen and (max-device-width : 425px){
  .input-card > input[type=text]{
    max-width: 300px;
  }
  .App{
    width : 400px;
  }
}
.searchbox{
  height : 50px;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.input-group{
  border-radius : 10px;
  border : 1px solid #555;
}
.input-group span{
  margin-right : 5px;
  margin-left : 3px;
}
.input-group input[type='text']{
  line-height: 18px;
  font-size : 15px;
  border-top-right-radius : 10px;
  border-bottom-right-radius: 10px;
  height : 20px;
  border : none;
  outline : none;
}
.input-group:focus{
  border-color :#66afe9;
}
.results{
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction : column;
      flex-direction : column;
}
.result{
  margin : 10px;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.result > *{
  padding : 0px 0 5px 0;
}
.result input[type='text']{
  width : 600px;
  overflow : hidden;
}

.result-head{
  font-size : 1.2em;
  display : -ms-flexbox;
  display : flex;
}
.tag{
  -ms-flex-item-align: center;
      align-self: center;
  padding-left : 2px;
  padding-right : 2px;
  padding-bottom : 3px;
  padding-top : 0;
  height : 15px;
  margin-left : 10px;
  font-size : 0.8em;
  border-radius : 40%;
  color : white;
}
.name{
  font-weight: bold;
}
.tag-css{
  background-color : #ff00ff;
}
.tag-js{
  background-color: #558fff;
}
.btn-clipboard{
  margin-left : 5px;
}
.desc{
  font-size : 0.8em;
}
.input-readonly{
  outline : none;
}
a{
  color : inherit;
}
a > i{
  margin-left : 10px;
}
.loading{
  margin-top : 30%;
  width : 100%;
  height : 100%;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align : center;
      align-items : center;
}


